(function() {
    'use strict';
    angular
        .module('angular')
        .controller('PaymentDataController', PaymentDataController);
        PaymentDataController.$inject = ['Payments','Order','DataCollection','BlumeStorage','$scope','$localStorage','Helper'];
    function PaymentDataController(Payments,Order,DataCollection,BlumeStorage,$scope,$localStorage,Helper) {
        var vm = this;
        init();
        ///////////////

        function init(){
            //Initialize Controller
            vm.saving = false;
            vm.useTasaCero = false;
            vm.acceptedPrivacyPolicy = false;
            vm.moneyTransferReceiptSent = false;
            vm.errorMessage = "";
            vm.payment = {orderId: 0, url:'', amount: 0, card: {number: '', expMonth: '', expYear: '', cvc: '', expDate: ''}};
            vm.currency = { value: 'CRC', symbol: '₡' };
            vm.paymentMethod = "";
            vm.months = ["01","02","03","04","05","06","07","08","09","10","11","12"];
            vm.years = ["2019","2020","2021","2022","2023","2024","2025","2026","2027","2028","2029","2030"];
            vm.uploader = BlumeStorage.createImageUploader();
            vm.moneyTransferReceiptUrl = "moneyTransferReceipt/";

            //Bind functions
            vm.makePayment = makePayment;
            
            getPaymentMethods();
        }

        BlumeStorage.broadcastUploadComplete($scope, function broadcastUpdate() {
            // Handle notification
            setTimeout(function(){
                vm.moneyTransferReceiptSent = true;
                $scope.$apply();
                }, 500);
        });

        function getPaymentMethods(){
            Helper.currency().then(function (results) { 
                vm.currency = results;
                if(vm.currency.value=='USD'){vm.currency.symbol='$'} else {vm.currency.symbol='₡'};
                Payments.availableMethods().then(function(data){ vm.paymentMethods = data; });
            });
        }

        function makePayment(){
          vm.saving = true;
          vm.errorMessage = "";
          if(vm.acceptedPrivacyPolicy==true){
            if(!$localStorage.deliveryType){
              vm.errorMessage = "Para continuar con tu compra debes escoger un método de entrega";
              vm.saving = false;
            } else if(!$localStorage.customerComplete){
              vm.errorMessage = "Para continuar con tu compra debes completar tus datos personales";
              vm.saving = false;
            } else if(!$localStorage.addressComplete){
              vm.errorMessage = "Para continuar con tu compra debes completar tus datos de envío";
              vm.saving = false;
            } else if(vm.paymentMethod==''){
              vm.errorMessage = "Para continuar con tu compra debes escoger un método de pago";
              vm.saving = false;
            } else {
              Order.create(vm.currency.value,vm.paymentMethod).then(function(data){
                vm.payment.orderId = data.id;
                vm.payment.amount = data.total;
                vm.payment.url = data.urlCode;
                vm.moneyTransferReceiptUrl += data.urlCode + "/";
                BlumeStorage.moneyTransferReceipt.url = vm.moneyTransferReceiptUrl;
                switch (vm.paymentMethod) {
                  case "paypal":
                      DataCollection.logPayment(vm.payment.orderId,vm.paymentMethod,"PayPal").then(function(data){ 
                        if(vm.currency.value=='CRC'){
                          vm.payment.amount = vm.payment.amount / 600;
                        }
                        document.getElementById("PayPalPost").submit(); 
                        vm.saving = false;
                      });
                    break;
                  case "bacsanjose":
                      DataCollection.logPayment(vm.payment.orderId,vm.paymentMethod,vm.payment.card.number).then(function(data){
                        if(vm.useTasaCero==true){
                          vm.paymentMethods.bac.processorId="grupocachostc3";
                        }
                        vm.payment.card.expDate = vm.payment.card.expMonth + vm.payment.card.expYear;
                        vm.paymentMethods.bac.timestamp = Math.round((new Date()).getTime() / 1000);
                        vm.paymentMethods.bac.hash = Payments.bacCreateMd5Hash(vm.payment.orderId,vm.payment.amount,vm.paymentMethods.bac.timestamp,vm.paymentMethods.bac.applicationPassword);
                        setTimeout(function(){
                            document.getElementById("CredomaticPost").submit();
                            vm.saving = false;
                            }, 2000);
                      });
                    break;
                  case "fttech":
                    DataCollection.logPayment(vm.payment.orderId,vm.paymentMethod,vm.payment.card.number).then(function(data){ 
                      Payments.sendPaymentFttech(vm.payment.orderId,vm.currency.value,vm.payment.amount,vm.payment.card,vm.paymentMethods.fttech).then(function(data){ vm.saving = false; });
                    });
                    break;
                  case "cybersource":
                    DataCollection.getClientInfo().then(function(response){
                      var clientInfo = response; 
                      var deviceFingerprintId = cybs_dfprofiler(vm.paymentMethods.cybersource.merchantId,"live");
                      Payments.sendPaymentCybersource(vm.payment.orderId,clientInfo,deviceFingerprintId,vm.payment.card).then(function(data){ vm.saving = false; });
                    })
                    break;
                  case "credix":
                    DataCollection.logPayment(vm.payment.orderId,vm.paymentMethod,"Credix").then(function(data){
                      var submitUrl = "https://webpay.credix.com/consumo#hash=" + vm.paymentMethods.credix.hash + "&referencia=" + vm.payment.orderId + "&monto=" + vm.payment.amount + ".00&moneda=188";
                      Payments.sendPaymentCredix(submitUrl).then(function(data){ vm.saving = false; });
                    });
                    break;
                  case "moneytransfer":
                    DataCollection.logPayment(vm.payment.orderId,vm.paymentMethod,"Transferencia").then(function(data){ 
                      vm.uploader.uploadAll();
                      vm.saving = false;
                    });
                    break;
                }
              },function(err){console.log(err); vm.saving = false; });
            }
          } else {
            vm.errorMessage = "Para continuar con tu compra debes aceptar las políticas de privacidad.";
            vm.saving = false;
          }
        }
    }
})();

(function() {
    'use strict';
    angular
        .module('angular')
        .controller('DeliveryDataController', DeliveryDataController);
        DeliveryDataController.$inject = ['LocationAutoComplete', 'Delivery','$localStorage','Helper'];
    function DeliveryDataController(LocationAutoComplete, Delivery,$localStorage,Helper) {
        var vm = this;
        init();
        ///////////////

        function init(){
            //Initialize Controller
            vm.addressComplete = false;
            vm.addressCreated = false;
            vm.showStorePickup = false;
            vm.showInternationalFares = false;
            vm.address = { id: 0, customerId: 0, isDefault: true };
            vm.provinces = LocationAutoComplete.costaRicaProvinces;
            vm.states = LocationAutoComplete.costaRicaStates;
            vm.cities = LocationAutoComplete.costaRicaCities;
            vm.countries = LocationAutoComplete.countries;
            vm.selectedFare= 0;
            vm.orderNote = "";
            vm.wrapGift = false;
            vm.currency = {value: '', symbol: ''}; 
            vm.isGrupoCachos = Helper.isGrupoCachos();
            vm.showWrapGift = Helper.showWrapGift();

            //Bind functions
            vm.verify = verify;
            vm.selectProvince = selectProvince;
            vm.selectCity = selectCity;
            vm.selectState = selectState;
            vm.selectCountry = selectCountry;
            vm.clearStateAndCity = clearStateAndCity;
            vm.getDeliveryCost = getDeliveryCost;
            vm.toggleDeliveryMethod = toggleDeliveryMethod;
            vm.toggleWrapGift = toggleWrapGift;
            delete $localStorage.addressComplete;

            Helper.currency().then(function (results) { 
                vm.currency = results;
                if(vm.currency.value=='USD'){vm.currency.symbol='$'} else {vm.currency.symbol='₡'};
            });

            getDeliveryMethods();
        }

        function verify(isValid){
            $localStorage.orderNote = vm.orderNote;
            $localStorage.wrapGift = vm.wrapGift;
            if(isValid){
                vm.addressComplete = true;
                $localStorage.addressComplete = true;
            } else {
                vm.addressComplete = false;
                delete $localStorage.addressComplete;
            }
            //Verify if customer already exists
            if(vm.addressComplete&&!vm.addressCreated){
                Delivery.createAddress(vm.address,vm.orderNote).then(function(response){ vm.addressCreated = true; });
            }
        }

        function getDeliveryMethods(){
            Delivery.availableMethods().then(function(data){ 
                vm.deliveryMethods = data;
                vm.selectedFare=vm.deliveryMethods[0].id;
                for (var i = 0; i < vm.deliveryMethods.length; i++) {
                    if(vm.deliveryMethods[i].deliveryType=="storepickup"){
                        vm.showStorePickup = true;
                    } else if(vm.deliveryMethods[i].deliveryType=="international"){
                        vm.showInternationalFares = true;
                    }
                }
             });
        }

        function toggleDeliveryMethod(selectedMethod){
            vm.addressComplete = false;
            vm.deliveryMethod = selectedMethod;
            Delivery.reset(selectedMethod,vm.selectedFare,vm.deliveryMethods);
            if(selectedMethod=="international"){
                Delivery.getInternationalDeliveryCost(vm.address.country,vm.currency.value);
            }
        }
        function toggleWrapGift(){
            $localStorage.wrapGift = vm.wrapGift; 
        }

        function clearStateAndCity(){
            vm.selectedState = "";
            vm.selectedCity = "";
        }

        function selectProvince(province){
            vm.address.country = province.name;
        }

        function selectState(state){
            vm.address.state = state.name;
        }

        function selectCity(city){
            vm.address.city = city.name;
        }

        function selectCountry(country){
            vm.address.country = country.name;
            Delivery.getInternationalDeliveryCost(country.name,vm.currency.value);
        }

        function getDeliveryCost(method){
            if(vm.deliveryMethod!="international"){
                Delivery.getDeliveryCost(method);
            }
        }

    }
})();

