(function() {
  'use strict';

  angular
    .module('angular')
    .controller('CartReviewController', CartReviewController);
  CartReviewController.$inject = ['$stateParams','Cart','$state','Discount','Delivery','Helper','ngCart'];
  function CartReviewController($stateParams,Cart,$state,Discount,Delivery,Helper,ngCart) {
    var vm = this;

    init();
    ///////////////

    function init(){
      //Initialize Controller
      vm.cartId = "";
      if($stateParams.cartId){vm.cartId = $stateParams.cartId;}
      Discount.reset();
      Delivery.restart();
      vm.currency = {value: '', symbol: ''}; 
      vm.errorMessage = "";
      
      Cart.verifyStock();

      //Bind Functions
      vm.continueToCheckout = continueToCheckout;

      
      Helper.currency().then(function (results) { 
        vm.currency = results;
        if(vm.currency.value=='USD'){vm.currency.symbol='$'} else {vm.currency.symbol='₡'};
      });
    }

    function continueToCheckout (){
      if(Cart.isAvailable()){ 
        //$state.go('checkout'); 
        //Delete After NuevoMundo Promo is over
        var site = Helper.currentSite();
        var total = ngCart.totalCost();
        if(site=="nmnuevomundo.com"&&total<5000){
          vm.errorMessage = "Para continuar con la promoción debes escoger al menos dos piezas";
        } else {
          vm.errorMessage = "";
          $state.go('checkout'); 
        }
      }
    }

  }
})();

(function() {
  'use strict';

  angular
    .module('angular')
    .run(runBlock);

  /** @ngInject */
  function runBlock($rootScope, ngCart,$transitions, Helper, $mdMenu, $mdSidenav,$timeout) {
    $rootScope.cart = ngCart;

    $rootScope.$on('$locationChangeSuccess', function() {
        document.body.scrollTop = document.documentElement.scrollTop = 0;
    });

    $rootScope.$on('$locationChangeStart', function() {
      $timeout(function(){
        $mdMenu.hide();
        $mdSidenav('left').close()
                    .then(function () {
                    });
        $rootScope.$emit('urlChanged');
      },200)
  });

    window.dataLayer = window.dataLayer || [];
    function gtag(){dataLayer.push(arguments);}
    gtag('js', new Date());

    Helper.googleAnalyticsId().then(function(response){ gtag('config', response.id); });

    $transitions.onStart( {}, function (trans) {
      var to = trans.to().name;
      // Google Analytics during state change
      gtag('set', 'page', to);
      gtag('set', 'title', to);
      gtag('send', 'pageview');
    });
  }
})();
